// Configuration globale 
@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nova+Round&display=swap');

$font-1: 'Nova Round', cursive;
$font-2: 'Dosis', sans-serif;
$font-3: 'Josefin Sans', serif;

$color-1: #2a1d52;
$color-2: #6C77F4;
$color-3: rgb(99, 164, 233);
$color-4: #4FEDD2;
$color-5: #36D0EA;
$white: #ddd;
$black: #333;

$borderRadius: 25px;
$shadow: 0 1px 3px #333;
$minHeight: 95vh;

// mixin pour aligner verticalement le contenu
@mixin verticalCenter {
    display: flex;
    justify-content: center;
    align-items: center; 
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    margin: 1rem auto;
    max-width: 1400px;
    font-family: $font-1;
    color: $black;
}
button {
    cursor: pointer;
}

li {
    list-style-type: none;
    color: $color-1; 
}

a {
    text-decoration: none;
    color: $black;
    cursor: pointer;
}
a:visited {
    color:$black;
}
.home, .competences, .portfolio, .contact {
    display: flex;
  
    @media screen and (max-width: 850px){
      display: block !important;
    }
}

.homeContent, .competencesContent,
.portfolioContent, .contactContent {
    margin-left: .4rem;
  
    @media screen and (max-width: 850px){
      margin-left: 0 !important;
    }
}